import React, { Component } from "react";
import { ContainerView } from "./ContainerView";
import { LayoutContext } from "./LayoutContext";
import { ThemeProvider } from "styled-components";

class Container extends Component {
  constructor(props) {
    super(props);
    // let windowGlobal = typeof window !== "undefined" && window;
    // console.log(
    //   "windowGlobal ",
    //   windowGlobal ? windowGlobal.localStorage.getItem("theme") : "not-avail"
    // );
    this.state = {
      y: 0,
      theme: "light"
    };
    // console.log("my state ", this.state.theme);
  }

  getInitialTheme = () => {
    let windowGlobal = typeof window !== "undefined" && window;

    return windowGlobal
      ? windowGlobal.localStorage.getItem("theme") || "light"
      : "light";
  };

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
    this.setState({ theme: this.getInitialTheme() });
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = () => {
    this.setState({ y: window.scrollY });
  };

  themeToggle = theme => {
    this.setState({ theme: theme });

    let windowGlobal = typeof window !== "undefined" && window;
    windowGlobal.localStorage.setItem("theme", theme);
  };

  render() {
    const value = {
      ...this.state,
      themeToggle: this.themeToggle
    };
    console.log("theme provider ", this.state.theme);
    return (
      <LayoutContext.Provider value={value}>
        <ThemeProvider theme={{ theme: this.state.theme }}>
          <ContainerView>{this.props.children}</ContainerView>
        </ThemeProvider>
      </LayoutContext.Provider>
    );
  }
}

export default Container;
